.about-us{
    margin-top: 100px;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.about-container1{
    width: 800px;
    display: grid;
    justify-content: center;
    align-items: center;
}
.container1-h1{
    display: flex;
    justify-content: center;
}
.container1-p{
    margin: 0;
    max-width: 600px;
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 20px;
    color: gray;
}

.about-container2{
    margin: 80px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 50px;
}
.container2-img{
    max-width: 600px;
    display: flex;
    overflow: hidden;
}
.container2-info{
    margin: 0;
    display: flex;
    flex-direction: column;
}
.container2-p,h4{
    color: gray;
}

@media screen and (max-width : 850px) {
    .about-us{
        margin: 50px 0;
        padding: 0 50px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .about-container1,.about-container2{
        margin: 0;
        padding: 20px;
        width: 100%;
        min-width: 280px;
        display: flex;
        flex-direction: column;
    }
    .container2-img{
        width: 100%;
        min-width: 300px;
    }
    .container2-info h4{
        margin: 0;
        display: flex;
        justify-content: end;
    }
    .container2-info h1{
        font-size: 28px;
    }
}